requirejs.config({

    paths: {
        "jquery": "lib/jquery/dist/jquery.min.js",
        "jqueryui": "lib/jquery-ui/jquery-ui.js",
        "jqueryslider": "lib/jquery-ui/ui/slider.js",
    },

    shim: {
        "jqueryui": ["jquery"],
        "jqueryslider": ["jqueryui", "jquery"],
    }
});

require([
    "lib/pym.js/src/pym.js",
    "text!_tooltip.html",
    "lib/jquery/dist/jquery.min.js",
    "lib/jquery-ui/jquery-ui.js",
    "lib/jquery-ui/ui/slider.js",
    "lib/landline/public/javascripts/landline.js",
    "lib/landline/public/javascripts/landline.stateline.js",
    "lib/landline/public/javascripts/states/states_packaged.js",
    "lib/raphael-min.js",
    "lib/underscore-min.js"
], function(pym,tooltip) {
    $(function() {

        // Initialize map and tooltip template
        var map = new Landline.Stateline(".map-container", "states");
        var tmpl = _.template(tooltip);

        new pym.Child({
            //polling: 500
        });

        // Make csv values turn into percentages

        var formatPercentage = function(s){
            s = (s * 100).toFixed(0) + "";
            return s + "%";
        };

        var fipsMap = {};
        var states = enrollments.filter(function(item) {
            return item.year == 2013;
        });

        var color = function(perc) {
            if (perc == '0') return "rgb(206,206,206)";
            if (perc < 0.1) return "hsl(52, 78, 82)";
            if (perc < 0.2) return "hsl(43, 83, 77)";
            if (perc < 0.3) return "hsl(37, 86, 73)";
            if (perc < 0.4) return "hsl(33, 89, 69)";
            if (perc < 0.5) return "hsl(30, 90, 64)";
            return "hsl(28, 92, 60)";
        };

        var shade = function(data) {
            fipsMap = {};
            data.forEach(function(state) {
                fipsMap[state.fips] = state;
                map.style(state.fips, "fill", color(state.perc));
            });
            map.reLayout();
        };

        shade(states);

        //gray initally

        map.on('mouseover', function(e, path, info) {
            info.existingStyle = (info.existingStyle || {});
            info.existingStyle["fill"] = path.attr("fill");
            info.existingStyle["strokeWidth"] = path.attr("stroke-width");
            path.attr("fill", "#999").attr("stroke-width", 1);
        });

        // Tooltip trigger

        map.on('mousemove', function(e, path, info) {


            if (!(info.fips in fipsMap)) return;
            $(".map-tooltips").html(tmpl({
                state: fipsMap[info.fips].state,
                perc: formatPercentage(fipsMap[info.fips].perc)
            })).css("left", e.pageX + 20).css("top", e.pageY + 20).show();
        });

        // Hide gray

        map.on('mouseout', function(e, path, data) {
            $(".map-tooltips").hide();
            _(data.existingStyle).each(function(v, k) {
                path.attr(k, v);
            });
        });

        // Make map

        map.createMap();

        // Make slider
 
        $( "#slider" ).slider({

            slide: function(event, ui, states) {
                var filtered = enrollments.filter(function(item){
                    return ui.value == item.year;
                });
                shade(filtered);
            },

            min: 2003,
            max: 2013,
            step: 1,
            value: 2013
        })
        .each(function() {
            //Add labels to slider with max, min values
            // Get options for slider (spec. above w/ value, min etc)
            var opt = $(this).data()["ui-slider"].options;

            //Get number of possible values
            var vals = opt.max - opt.min;

            //Position labels
            for (var i = 0; i <= vals; i++) {
                // Create new element and position with percentages
                var el = $('<label>' + (i + opt.min) + '</label>').css('left', (i/vals*100) + '%');
                //Add element insider #year-slider
                $('#slider').append(el);
            }
        });
    });
});
define("main", function(){});

